@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Narrow&display=swap);
.Devtools {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 33.3%;
  padding: 1rem 4rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  z-index: 1;
}

.Devtools__input {
  background: none;
  color: #AAAAAA;
  font-size: inherit;
  border-radius: .25rem;
  font-family: monospace;
  padding: 0;
  margin: 0;
  border: 0;
  line-height: 25px;
}

.Devtools__input:focus {
  outline: none;
}

.Devtools__log {
  font-family: monospace;
  font-size: inherit;
  color: #AAAAAA;
  white-space: pre;
  margin-bottom: auto;
}

.Devtools__log div {
  line-height: 25px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.Main {
  display: flex;
  flex: 1 1;
  overflow: hidden;
  position: relative;
}

.Static {
  flex: 1 1;
  padding: 2rem 4rem;
}

.Toolbar {
  pointer-events: none;
  display: flex;
  padding: 10px;
}

.Toolbar > * {
  pointer-events: all;
}

.Toolbar button + button {
  margin-left: 10px;
}

.Button {
  display: inline-block;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 1rem;
  color: var(--foreground-color);
  border: 1px solid transparent;
}

.Button:hover {
  opacity: 0.7;
}

.Button--is-muted {
  opacity: .5;
}

.Button--icon {
  line-height: 0;
}

.Button--text {
  padding: .3rem;
}

.Button--is-active {
  border-bottom-color: var(--foreground-color);
}

.Button[disabled] {
  pointer-events: none;
}

.Header {
  justify-content: flex-end;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  z-index: 10;
}

.Header__help {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  z-index: 11;
  background-color: rgba(0, 0, 0, .25);
  cursor: pointer;
}

.Header__help-contents {
  opacity: .75;
  cursor: default;
  margin: auto;
  padding: 1rem;
  background-color: var(--foreground-color);
  border-radius: .25rem;
  color: var(--background-color);
  min-width: 400px;
  min-height: 200px;
}

.TextGlitch {
  position: relative;
  margin: 0 auto;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.TextGlitch::before,
.TextGlitch::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  background-color: inherit;
  clip: rect(0, 100vw, 0, 0);
}

.TextGlitch::before {
  left: 0.06em;
  text-shadow: 1px 0 green;
  animation: glitch-effect 3s infinite linear alternate-reverse;
}

.TextGlitch::after {
  left: 0.03em;
  text-shadow: -1px 0 red;
  animation: glitch-effect 2s infinite linear alternate-reverse;
}

@-webkit-keyframes glitch-effect {
  0% { clip: rect(0.04em, 100vw, 0.58em, 0); }
  20% { clip: rect(0.73em, 100vw, 0.07em, 0); }
  40% { clip: rect(0.81em, 100vw, 0.34em, 0); }
  60% { clip: rect(0.66em, 100vw, 1.1em, 0); }
  80% { clip: rect(1.31em, 100vw, 0.99em, 0); }
  100% { clip: rect(0.07em, 100vw, 1.11em, 0); }
}

@keyframes glitch-effect {
  0% { clip: rect(0.04em, 100vw, 0.58em, 0); }
  20% { clip: rect(0.73em, 100vw, 0.07em, 0); }
  40% { clip: rect(0.81em, 100vw, 0.34em, 0); }
  60% { clip: rect(0.66em, 100vw, 1.1em, 0); }
  80% { clip: rect(1.31em, 100vw, 0.99em, 0); }
  100% { clip: rect(0.07em, 100vw, 1.11em, 0); }
}

.Graphics {
  flex: 1 1;
  display: flex;
}

.Graphics--perspective {
  perspective: var(--perspective);
}

.Graphics__layer-container {
  margin: auto;
  width: 80%;
  height: 80%;
  position: relative;
  transition: all .05s ease;
}

.Graphics__layer-container--transition {
  transition: all .2s ease;
}

.Graphics__layer {
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  position: absolute;
}

.Graphics__layer svg {
  position: absolute;
}

.Graphics__layer-container--perspective {
  transform-style: preserve-3d;
  transform:
    rotateX(calc(var(--raw-y) * 1deg * -1))
    rotateY(calc(var(--raw-x) * 1deg))
    translateZ(0);
}

.Graphics__layer-container--perspective .Graphics__layer {
  transform: translateZ(var(--translate-z));
}

.Graphics__layer-container--parallax-vertical .Graphics__layer {
  transform: translateY(var(--translate-y));
}

.Graphics__layer-container--parallax-horizontal .Graphics__layer {
  transform: translateX(var(--translate-x));
}

.Graphics__layer-container--parallax .Graphics__layer {
  transform:
    translateX(var(--translate-x))
    translateY(var(--translate-y))
    translateZ(0);
}

@-webkit-keyframes boat-wave {
  50% { transform: translateY(.5em) rotate(5deg); }
}

@keyframes boat-wave {
  50% { transform: translateY(.5em) rotate(5deg); }
}

.Boat {
  --animation-duration: 4s;
  -webkit-animation: boat-wave var(--animation-duration) calc(var(--animation-duration) * 0) ease infinite;
          animation: boat-wave var(--animation-duration) calc(var(--animation-duration) * 0) ease infinite;
}

.Boat:nth-of-type(5n + 1) { -webkit-animation-delay: calc(var(--animation-duration) * 0); animation-delay: calc(var(--animation-duration) * 0); }
.Boat:nth-of-type(5n + 2) { -webkit-animation-delay: calc(var(--animation-duration) * .5); animation-delay: calc(var(--animation-duration) * .5); }
.Boat:nth-of-type(5n + 3) { -webkit-animation-delay: calc(var(--animation-duration) * 1); animation-delay: calc(var(--animation-duration) * 1); }
.Boat:nth-of-type(5n + 4) { -webkit-animation-delay: calc(var(--animation-duration) * 1.5); animation-delay: calc(var(--animation-duration) * 1.5); }
.Boat:nth-of-type(5n + 5) { -webkit-animation-delay: calc(var(--animation-duration) * 2); animation-delay: calc(var(--animation-duration) * 2); }

.Chat {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.Chat__options {
  min-height: 150px;
  margin-bottom: 100px;
  text-align: right;
}

@-webkit-keyframes magic-8-ball-shake {
  10%, 90% { transform: translate3d(-1rem, 0, 0); }
  20%, 80% { transform: translate3d(2rem, 0, 0); }
  30%, 50%, 70% { transform: translate3d(-4rem, 0, 0); }
  40%, 60% { transform: translate3d(4rem, 0, 0); }
}

@keyframes magic-8-ball-shake {
  10%, 90% { transform: translate3d(-1rem, 0, 0); }
  20%, 80% { transform: translate3d(2rem, 0, 0); }
  30%, 50%, 70% { transform: translate3d(-4rem, 0, 0); }
  40%, 60% { transform: translate3d(4rem, 0, 0); }
}

.PageMagic8Ball--shake {
  -webkit-animation: magic-8-ball-shake 0.8s cubic-bezier(.36,.07,.19,.97) both;
          animation: magic-8-ball-shake 0.8s cubic-bezier(.36,.07,.19,.97) both;
  transform: translate3d(0, 0, 0);
}

.PageMagic8Ball__answer {
  opacity: 0;
  transform: rotate(-20deg) scale(0.93);
  transition: all .7s ;
  transition-timing-function: ease-out, cubic-bezier(.82, .1, .14, 1.12);
  pointer-events: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.PageMagic8Ball__answer-text {
  z-index: 1;
  position: relative;
  margin: auto;
  text-transform: uppercase;
  font-size: .95rem;
  font-weight: bold;
  text-shadow: -1px -1px 1px #000,
    1px 1px 1px rgba(255,255,255,.1);
  opacity: .7;
  font-family: 'PT Sans Narrow', sans-serif;
}

.PageMagic8Ball__answer-text::first-letter {
  font-size: 1.2rem;
}

.PageMagic8Ball__answer--ready {
  opacity: 1;
  transform: rotate(0) scale(1);
}

