.PageObabelie__backdrop {
  width: 80%;
  height: 80%;
  margin: 10%;
  cursor: pointer;
  pointer-events: all;
}

.PageObabelie__canvas {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

:root {
  --perspective: 60vmax;
  --translate-z-delta: 50px;
  --raw-x: 0;
  --raw-y: 0;
  --font-size: 18px;
  --background-color: #222222;
  --foreground-color: #EEEEEE;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  color: #EEEEEE;
  color: var(--foreground-color);
  font-size: 18px;
  font-size: var(--font-size);
  line-height: 1.4;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

button {
  margin: 0;
  padding: 0;
  border: 0;
  background: 0;
  outline: none;
  font-size: inherit;
}

p {
  margin: 0 0 1rem 0;
}

body::before {
  z-index: -1;
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: #222222;
  background-color: var(--background-color);
  pointer-events: none;
  transition: background-color .5s ease;
}

#root {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.abzac::first-letter {
  padding-left: 3rem;
}

a {
  color: inherit;
  text-decoration: none;
}

